// React and Next
import { useEffect } from "react";
import { useTranslation } from "react-i18next";

// Components

const ProductDetailTabs = ({
  addonlist,
  activeLink,
  setActiveLink,
  tabChangeScroll,
  productDetail,
  reviewAndRatingCount,
  sectionMap,
}) => {
 const { t } = useTranslation();

  useEffect(() => {
    const handleScroll = () => {
      const scrollPos = window.scrollY;

      for (const [selector, ref] of Object.entries(sectionMap)) {
        const section = ref.current;

        if (section) {
          const elementTop = section.offsetTop;
          const elementHeight = section.offsetHeight;

          if (
            elementTop - 150 <= scrollPos &&
            elementTop + elementHeight >= scrollPos
          ) {
            setActiveLink(selector);
          }
        }
      }
    };

    // Attach scroll event listener
    window.addEventListener("scroll", handleScroll);

    // Cleanup event listener on component unmount
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  return (
    <div className="top-[70px] sm:top-[80px] lg:top-[129px] z-10 sticky flex flex-nowrap justify-start sm:justify-center items-center space-x-8 lg:space-x-16 bg-white mb-0 pt-6 pb-1 sm:pb-6 border-grey200 border-b overflow-auto whitespace-nowrap explore-link">
      <a
        className={
          activeLink == "#section1"
            ? "pb-3 border-black400 cursor-pointer border-b text-base 2xl:text-lg font-semibold hover:text-black text-black-300 rtl:ml-8 lg:rtl:ml-16"
            : "pb-3 border-black400 cursor-pointer text-grey-400 text-base 2xl:text-lg font-semibold hover:text-black rtl:ml-8 lg:rtl:ml-16"
        }
        onClick={() => tabChangeScroll("#section1")}
        role="button"
      >
        {t("productdetails.Description")}
      </a>

      {productDetail?.isSimplified != 2 &&
        addonlist &&
        addonlist?.["product-attribute"] &&
        addonlist?.["product-attribute"]?.status == 1 && (
          <a
            className={
              activeLink == "#section2"
                ? "pb-3 border-black400 cursor-pointer border-b text-base 2xl:text-lg font-semibold hover:text-black text-black-300"
                : "pb-3 border-black400 cursor-pointer text-grey-400 text-base 2xl:text-lg font-semibold hover:text-black"
            }
            onClick={() => tabChangeScroll("#section2")}
            role="button"
          >
            {t("productdetails.Specification")}
          </a>
        )}

      {addonlist &&
        addonlist?.["rating-review"] &&
        addonlist?.["rating-review"]?.status == 1 && (
          <a
            className={
              activeLink == "#section3"
                ? "pb-3 border-black400 cursor-pointer border-b text-base 2xl:text-lg font-semibold hover:text-black text-black-300 flex items-center space-x-2 rtl:space-x-[unset]"
                : "pb-3 border-black400 cursor-pointer text-grey-400 text-base 2xl:text-lg font-semibold hover:text-black flex items-center space-x-2 rtl:space-x-[unset]"
            }
            onClick={() => tabChangeScroll("#section3")}
            key="4"
            role="button"
          >
            <p className="rtl:ml-2">{t("myprofile.PReviews")}</p>
            <span className="place-items-center grid bg-grey-300 px-1.5 py-1 rounded-full min-w-[28px] min-h-[28px] font-normal text-black-300 text-sm">
              {reviewAndRatingCount &&
                reviewAndRatingCount?.reviewCount &&
                reviewAndRatingCount?.reviewCount}
            </span>
          </a>
        )}

      {addonlist &&
        addonlist?.["question-answer"] &&
        addonlist?.["question-answer"]?.status == 1 && (
          <a
            className={
              activeLink == "#section4"
                ? "pb-3 border-black400 cursor-pointer border-b text-base 2xl:text-lg font-semibold hover:text-black text-black-300"
                : "pb-3 border-black400 cursor-pointer text-grey-400 text-base 2xl:text-lg font-semibold hover:text-black"
            }
            onClick={() => tabChangeScroll("#section4")}
            role="button"
          >
            {t("myprofile.faq")}
          </a>
        )}
    </div>
  );
};

export default ProductDetailTabs;
