export default function Description({ productDetail, lang }) {

  const decodeHtmlEntities = (str) => {
    if(typeof window !=='undefined'){
    const txt = document.createElement("textarea");
    txt.innerHTML = str;
    return txt.value;
    }
  };

  const description = decodeHtmlEntities(
    productDetail?.productDescriptionTrans
      ? productDetail?.productDescriptionTrans
      : productDetail?.description
  );

  return (
    <div>
      <h4 className="mb-8 text-2xl font-black text-black">
        {lang("productdetails.Description")}
      </h4>
      <div className="mt-8">
        {/* Backup Style */}
        {/* font-normal text-black-300 text-lg list-default bullet */}
        <div
          className="text-black-300 list-default bullet editor-style"
          dangerouslySetInnerHTML={{
            __html: description
              ?.replaceAll("&amp;", "&")
              .replaceAll("&lt;", "<")
              .replaceAll("&gt;", ">")
              .replaceAll("&quot;", '"')
              .replaceAll("&#39;", "'")
              .replaceAll("&sbquo;", "‚")
              .replaceAll("&#61;", "=")
              .replaceAll("&#45;", "-")
              .replaceAll("&hellip;", "…")
              .replaceAll("&commat;", "@")
              .replaceAll("&copy;", "©")
              .replaceAll("&#35;", "#")
              .replaceAll("&ldquo;", "“")
              .replaceAll("&rsquo;", "’")
              .replaceAll("&lsquo;", "‘")
              .replaceAll("&trade;", "™")
              .replaceAll("&reg;", "®")
              .replaceAll("&ndash;", "–")
              .replaceAll("&eacute;", "é")
              .replaceAll("&euro;", "€")
              .replaceAll("&pound;", "£")
              .replace("&lt;/p&gt;&amp;&#35;10;&amp;&#35;10;&lt;p&gt;", ""),
          }}
        />
      </div>
    </div>
  );
}
