// 3rd Party
// Redux
import { useSelector } from "react-redux";

// Components
import Breadcrumb from "@/Components/Breadcrumbs";
import { useTranslation } from "react-i18next";

const ProductDetailBreadcrumb = ({ productDetail }) => {
  const { t } = useTranslation();
  const subnames = useSelector((state) => state.userReducer.subcatnames);
  const productname = useSelector((state) => state.userReducer.categoryname);
  const slugname = useSelector((state) => state.userReducer.categoryslug);
  const ids = useSelector((state) => state.userReducer.subid);

  const allcategory = useSelector(
    (state) => state.userReducer.allcategorybread
  );
  const prosub = useSelector((state) => state.userReducer.productsub);
  const breadstatus = useSelector(
    (state) => state.userReducer.productbreadcrumb
  );

  const breadCrumb = [
    {
      text: t("Breadcrumb.home"),
      url: "/",
    },
    {
      text: productDetail?.name,
      url: "javascript:void(0)",
    },
  ];

  const breadCrumbsub = [
    {
      text: t("Breadcrumb.home"),
      url: "/",
    },
    {
      text: t("Breadcrumb.allCategory"),
      url: "/category",
    },
    {
      text: subnames,
      url: `/sub-categorys/${ids}`,
    },
    {
      text: productname,
      url: `/product/${slugname}`,
    },
    {
      text: productDetail?.name,
      url: "javascript:void(0)",
    },
  ];

  const breadCrumbsall = [
    {
      text: t("Breadcrumb.home"),
      url: "/",
    },
    {
      text: t("Breadcrumb.allCategory"),
      url: "/category",
    },
    {
      text: productname,
      url: `/product/${slugname}`,
    },
    {
      text: productDetail?.name,
      url: "javascript:void(0)",
    },
  ];

  const breadCrumbs = [
    {
      text: t("Breadcrumb.home"),
      url: "/",
    },
    {
      text: productname,
      url: `/product/${slugname}`,
    },
    {
      text: productDetail?.name,
      url: "javascript:void(0)",
    },
  ];

  return (
    <Breadcrumb
      breadCrumb={
        breadstatus
          ? breadCrumb
          : prosub
          ? breadCrumbsub
          : allcategory
          ? breadCrumbsall
          : breadCrumbs
      }
    />
  );
};

export default ProductDetailBreadcrumb;
