// React and Next
import { useState } from "react";

// API
import { addToCartApi } from "@/api/serversideaction/cartapi";

// Utils
import { AddToCartLocal } from "@/utilities/AddToCart";

// 3rd Party
import { useRouter } from "nextjs-toploader/app";
// Redux
import { useDispatch, useSelector } from "react-redux";
import { enableBackOrder, enableFailedOrder } from "@/redux/slices/checkout";
import { quotelist } from "@/redux/slices/userSlice";

// Components
import ToastMessage from "@/Components/ToastMessage";
import { useTranslation } from "react-i18next";

const ProductOrderButtons = ({
  token,
  addonlist,
  productDetail,
  defaultImage,
}) => {
  const defalut = 1;
  const { t } = useTranslation();
  const router = useRouter();
  const dispatch = useDispatch();

  const [btBuyNowLoader, setBtBuyNowLoader] = useState(false);
  const [btAddtoCartLoader, setAddtoCartLoader] = useState(false);
  const [btSendQuotaLoader, setSendQuotaLoader] = useState(false);

  const qoutedetails = useSelector((state) => state.userReducer.quote);

  // Buy now and Ad to cart button function
  const handleToCart = (datacart, button) => {
    if (btBuyNowLoader && btAddtoCartLoader) return;

    if (token) {
      let cartlist = JSON.parse(localStorage.getItem("cartItems"));
      const qyt =
        datacart?.hasStock == 0
          ? 1
          : datacart?.minQuantityAllowedCart
          ? datacart?.minQuantityAllowedCart
          : 1;

      if (cartlist.length) {
        const inLocalCart = cartlist.find(
          (product) => product?.skuId == datacart?.skuId
        );
        if (
          inLocalCart &&
          inLocalCart?.productCount != datacart?.maxQuantityAllowedCart
        ) {
          // Turning loader on for buy now or add to cart button
          button == "buynow"
            ? setBtBuyNowLoader(true)
            : setAddtoCartLoader(true);

          addToCartApi(
            inLocalCart?.productCount + qyt,
            "",
            datacart,
            dispatch,
            button,
            button == "buynow" ? setBtBuyNowLoader : setAddtoCartLoader,
            router
          );
        } else {
          // Turning loader on for buy now or add to cart button
          button == "buynow"
            ? setBtBuyNowLoader(true)
            : setAddtoCartLoader(true);

          addToCartApi(
            qyt,
            "new",
            datacart,
            dispatch,
            button,
            button == "buynow" ? setBtBuyNowLoader : setAddtoCartLoader,
            router
          );
        }
      } else {
        // Turning loader on for buy now or add to cart button
        button == "buynow" ? setBtBuyNowLoader(true) : setAddtoCartLoader(true);

        addToCartApi(
          qyt,
          "new",
          datacart,
          dispatch,
          button,
          button == "buynow" ? setBtBuyNowLoader : setAddtoCartLoader,
          router
        );
      }
    } else {
      AddToCartLocal(datacart, dispatch);
      button == "buynow" && router.push("/checkout");
      ToastMessage({ type: "success", message: t("toastMessage.addToCart") });
    }
    dispatch(enableBackOrder(false));
    dispatch(enableFailedOrder(false));
  };

  const handlequote = (data) => {
    if (btSendQuotaLoader) return;

    if (token == undefined) {
      router.push("/buyer-login");
    } else {
      let arr = [];
      if (qoutedetails?.length > 0) {
        let result = qoutedetails?.find(function (item) {
          return item?.skuId === data?.skuId;
        });
        if (result) {
          let secondquantity = "";
          let qan = qoutedetails?.filter((res) => res?.skuId !== data?.skuId);
          qoutedetails?.map((datas) => {
            if (datas?.skuId === data?.skuId) {
              secondquantity = datas?.quantity + defalut;
              let asd = [];
              asd.push({ ...datas, quantity: secondquantity });
              let a = qan.concat(asd);
              dispatch(quotelist(a));
              setSendQuotaLoader(true);
              ToastMessage({
                type: "dark",
                message: t("toastMessage.addToQuotation"),
                routeMsg: t("toastMessage.view"),
              });
              setTimeout(setSendQuotaLoader(false), 2500);
            }
          });
        } else {
          let object = {};
          object.quantity = defalut;
          object.image = defaultImage?.image;
          object.containerName = defaultImage?.containerName;
          object.name = data?.name;
          object.defaultPrice = data?.discount;
          object.productId = data?.productId;
          object.price = data?.price;
          object.productSlug = data?.productSlug;
          object.sku = data?.skuName;
          object.vendorId = data?.vendorId;
          object.productNameTrans = data?.productNameTrans;
          object.skuId = data?.skuId;
          object.taxType = data?.taxType;
          object.taxValue = data?.taxValue;
          object.flag = data?.flag;
          object.pricerefer = data?.pricerefer;
          let a = qoutedetails.concat([object]);
          const array = qoutedetails;
          dispatch(quotelist(a));
          setSendQuotaLoader(true);
          ToastMessage({
            type: "dark",
            message: t("toastMessage.addToQuotation"),
            routeMsg: t("toastMessage.view"),
          });
          setTimeout(setSendQuotaLoader(false), 2500);
        }
      } else {
        let object = {};
        object.quantity = defalut;
        object.image = defaultImage?.image;
        object.containerName = defaultImage?.containerName;
        object.name = data?.name;
        object.defaultPrice = data?.discount;
        object.productId = data?.productId;
        object.price = data?.price;
        object.productSlug = data?.productSlug;
        object.sku = data?.skuName;
        object.vendorId = data?.vendorId;
        object.productNameTrans = data?.productNameTrans;
        object.skuId = data?.skuId;
        object.taxType = data?.taxType;
        object.taxValue = data?.taxValue;
        object.flag = data?.flag;
        object.pricerefer = data?.pricerefer;
        arr.push(object);
        dispatch(quotelist(arr));
        setSendQuotaLoader(true);
        ToastMessage({
          type: "dark",
          message: t("toastMessage.addToQuotation"),
          routeMsg: t("toastMessage.view"),
        });
        setTimeout(setSendQuotaLoader(false), 2500);
      }
    }
  };

  // Back order button function
  const handleBackOrder = (productDetail) => {
    if (
      productDetail?.availableBackOrderStock <
      productDetail?.maxQuantityAllowedCart
    ) {
      productDetail.productCount = productDetail.minQuantityAllowedCart;
      productDetail.priceGroup = 0;
      productDetail.maxQuantityAllowedCart =
        productDetail?.availableBackOrderStock;
      let product = [];
      product[0] = productDetail;
      dispatch(enableBackOrder(true));
      dispatch(enableFailedOrder(false));
      localStorage.setItem("backOrder", JSON.stringify(product));
      router.push("/stock-checkout");
    } else {
      productDetail.productCount = productDetail.minQuantityAllowedCart;
      productDetail.priceGroup = 0;
      let product = [];
      product[0] = productDetail;
      dispatch(enableBackOrder(true));
      dispatch(enableFailedOrder(false));
      localStorage.setItem("backOrder", JSON.stringify(product));
      router.push("/stock-checkout");
    }
  };

  const BuyNowAddToCartButtons = () => (
    <div className="flex flex-row sm:flex-col space-x-4 rtl:space-x-[unset] sm:space-x-0 space-y-[unset] sm:space-y-4">
      {/* Buy now button */}
      <a
        onClick={(e) => handleToCart(productDetail, "buynow")}
        className="flex justify-center items-center hover:bg-slate-50 rtl:ml-4 sm:rtl:ml-0 border border-boldblack rounded w-full h-[49px] font-bold text-boldblack text-base cursor-pointer btn"
      >
        {btBuyNowLoader ? (
          <ButtonSpinnerLoader />
        ) : (
          t("productdetails.Buynow")
        )}
      </a>

      {/* Add to cart button */}
      <a
        onClick={(e) => handleToCart(productDetail, "addtocart")}
        role="button"
        className="flex justify-center items-center hover:bg-slate-50 border border-boldblack rounded w-full h-[49px] font-bold text-boldblack text-base cursor-pointer btn"
      >
        {btAddtoCartLoader ? <ButtonSpinnerLoader /> : t("myprofile.addcart")}
      </a>
    </div>
  );

  const SendQuotationButton = () =>
    addonlist &&
    addonlist?.["product-quotation"] &&
    addonlist?.["product-quotation"]?.status == 1 && (
      <button
        onClick={() => handlequote(productDetail)}
        className="flex justify-center items-center bg-orange-500 rounded h-[49px] font-bold text-white text-base btn"
      >
        {btSendQuotaLoader ? (
          <ButtonSpinnerLoader />
        ) : (
          t("myprofile.sendquote")
        )}
      </button>
    );

  return (
    <div className="fixed bottom-0 left-0 z-40 flex flex-col w-full p-3 mb-0 space-y-4 bg-white border-t sm:static sm:mb-10 sm:p-0 border-slate-200 sm:border-t-0">
      {productDetail?.productType == "virtual" ? (
        <BuyNowAddToCartButtons />
      ) : productDetail?.stockStatus == "inStock" &&
        productDetail?.quantity > 0 &&
        productDetail?.outOfStockThreshold < productDetail?.quantity ? (
        <>
          <BuyNowAddToCartButtons />

          <SendQuotationButton />
        </>
      ) : (
        <>
          <div className="flex flex-row sm:flex-col space-x-4 rtl:space-x-[unset] sm:space-x-0 space-y-[unset] sm:space-y-4">
            <a
              role="button"
              className="flex justify-center items-center bg-[#e8e8e8] hover:bg-[#e8e8e8] rounded w-full h-[49px] font-bold text-[#00000096] text-base uppercase cursor-default btn "
            >
              {t("productdetails.OutOfStk")}
            </a>

            {/* Back order button */}
            {token &&
              productDetail?.enableBackOrders == 1 &&
              productDetail?.availableBackOrderStock > 0 && (
                <a
                  role="button"
                  onClick={(e) => handleBackOrder(productDetail)}
                  className="flex justify-center items-center hover:bg-slate-50 border border-boldblack rounded w-full h-[49px] font-bold text-boldblack text-base uppercase cursor-pointer btn"
                >
                  {t("productdetails.OrdNow")}
                </a>
              )}
          </div>

          <SendQuotationButton />
        </>
      )}
    </div>
  );
};

export default ProductOrderButtons;

