import { useEffect, useRef, useState } from "react";
import { createPortal } from "react-dom";

const MagnifierPreview = () => {
  const [magnifierData, setMagnifierData] = useState({
    show: false,
    position: { x: 0, y: 0 },
    imageSrc: "",
    dimensions: { width: 0, height: 0 },
  });

  const containerRef = useRef(null);
  const [previewSize, setPreviewSize] = useState(0);
  const zoom = 2.5;

  useEffect(() => {
    const handler = (e) => {
      const { show, position, imageSrc, dimensions } = e.detail;

      setMagnifierData({
        show,
        position: { ...position },
        imageSrc:
          typeof imageSrc === "string"
            ? imageSrc
                .replaceAll(" ", "%20")
                .replaceAll("(", "%28")
                .replaceAll(")", "%29")
            : "",
        dimensions: { ...dimensions },
      });
    };

    window.addEventListener("update-magnifier", handler);
    return () => window.removeEventListener("update-magnifier", handler);
  }, []);

  useEffect(() => {
    const updateSize = () => {
      if (containerRef.current) {
        setPreviewSize(containerRef.current.offsetWidth);
      }
    };

    updateSize();
    window.addEventListener("resize", updateSize);
    return () => window.removeEventListener("resize", updateSize);
  }, []);

  const { show, position, dimensions, imageSrc } = magnifierData;

  const preview = (
    <div
      ref={containerRef}
      id="magnifier-preview-target"
      className={`fixed hidden overflow-hidden rounded-md border-2 border-solid border-black bg-white shadow-lg transition-opacity duration-300 ease-in-out sm:block ${
        show ? "visible opacity-100" : "invisible opacity-0"
      }`}
      style={{
        position: "fixed",
        zIndex: 1000,
        width: "38vw",
        height: "38vw",
        right: "20px",
        top: "55%",
        transform: "translateY(-50%)",
        backgroundImage: `url(${imageSrc})`,
        backgroundRepeat: "no-repeat",
        backgroundSize: `${dimensions.width * zoom}px ${
          dimensions.height * zoom
        }px`,
        backgroundPosition: `
          ${Math.min(0, previewSize / 2 - position.x * zoom)}px
          ${Math.min(0, previewSize / 2 - position.y * zoom)}px
        `,
        backgroundColor: "#fff",
      }}
    />
  );

  if (typeof document !== "undefined") {
    return createPortal(preview, document.body);
  }

  return preview;
};

export default MagnifierPreview;
