// React and Next
import Link from "next/link";

// Utils
import { imageUrl } from "@/utilities/url";

// Components
import { useTranslation } from "react-i18next";

const ProductVendor = ({ productDetail }) => {
 const { t } = useTranslation();

  return (
    productDetail?.vendorCompanyName && (
      <div className="flex items-center space-x-2 rtl:space-x-[unset] bg-greenlight mb-6 p-4 border border-lightgreen rounded">
        <div className="w-10 h-10 rtl:ml-2">
          <img
            src={
              imageUrl +
              "?path=" +
              productDetail?.companyLogoPath +
              "&name=" +
              productDetail?.companyLogo +
              "&width=60&height=60"
            }
            alt={productDetail?.vendorName}
            className="w-full h-full"
            onError={({ currentTarget }) => {
              currentTarget.onerror = null;
              currentTarget.src = "/img/user1.jpg";
              currentTarget.alt = "user";
            }}
          />
        </div>
        <div className="flex flex-col space-y-1.5">
          <Link
            prefetch={false}
            href={`/shop/${productDetail?.vendorDisplayNameUrl}`}
            className="z-10 text-base font-semibold text-black-300 hover:text-blue-500"
          >
            {productDetail?.vendorName}
          </Link>
          <div className="flex items-center space-x-3">
            <p className="text-xs font-normal text-black-100">
              {productDetail?.vendorCompanyCountry}
            </p>
            {productDetail?.companyTaxNumber !== "" && (
              <span className="text-xs font-normal text-black-100">
                {t("myprofile.GST")}: {productDetail?.companyTaxNumber}
              </span>
            )}
          </div>
        </div>
      </div>
    )
  );
};

export default ProductVendor;
