"use client";

// React and Next
import { useEffect, useRef, useState } from "react";

// Components
import ConnectPlugin from "../connectPlugins";
import Description from "./Description";
import Contactus from "../Contactus";
import ProductPrice from "./ProductPrice";
import ProductImageVideo from "./ProductImageVideo";
import ProductDetailBreadcrumb from "./ProductDetailBreadcrumb";
import GroupedProducts from "./GroupedProducts";
import ProductHighlights from "./ProductHighlights";
import ProductDetailTabs from "./ProductDetailTabs";
import ProductVendor from "./ProductVendor";
import ProductOrderButtons from "./ProductOrderButtons";
import ProductDetailSkeleton from "./skeleton";
import { useTranslation } from "react-i18next";

export default function ProductDetail({ token, rawProduct, slug, addonlist }) {
  const { t } = useTranslation();

  const description = useRef(1);
  const specification = useRef(2);
  const Review = useRef(4);
  const QuesAndAnswer = useRef(5);

  const sectionMap = {
    "#section1": description,
    "#section2": specification,
    "#section3": Review,
    "#section4": QuesAndAnswer,
  };

  const categoryIds = rawProduct?.Category?.map(
    (category) => category?.categoryId,
  );

  const [productDetail, setProductDetail] = useState({ ...rawProduct } || {});

  const [isProductDetailLoading, setIsProductDetailLoading] = useState(
    addonlist?.["product-variants"]?.status == 1 &&
      rawProduct?.isSimplified == 0
      ? true
      : false,
  );
  const [defaultImage, setDefaultImage] = useState({});
  const [selectedImageIndex, setSelectedImageIndex] = useState(0);
  const [reviewAndRatingCount, setReviewAndRatingCount] = useState();
  const [activeLink, setActiveLink] = useState("");

  useEffect(() => {
    if (rawProduct) {
      productDetail?.productOriginalImage &&
        productDetail?.productOriginalImage?.map((img, index) => {
          if (img?.defaultImage == 1) {
            setDefaultImage(JSON.parse(JSON.stringify(img)));
            setSelectedImageIndex(index + 1);
          }
        });
    }
  }, [productDetail?.skuId]);

  const tabChangeScroll = (current) => {
    const scroll = (ref) => {
      const yOffset = -150;
      const y =
        ref.current.getBoundingClientRect().top + window.pageYOffset + yOffset;
      window.scrollTo({ top: y, behavior: "smooth" });
    };

    setActiveLink(current);

    if (current === "#section1") {
      scroll(description);
    } else if (current === "#section2") {
      scroll(specification);
    } else if (current === "#section3") {
      scroll(Review);
    } else if (current === "#section4") {
      scroll(QuesAndAnswer);
    }
  };

  return (
    <>
      {isProductDetailLoading && <ProductDetailSkeleton />}

      <div
        className={`m-auto max-w-screen-2xl px-4 py-2.5 ${isProductDetailLoading ? "hidden" : "block"}`}
      >
        <ProductDetailBreadcrumb productDetail={productDetail} />

        <div className="grid-cols-16 mb-20 mt-6 grid flex-col items-start gap-4 lg:flex-row">
          <ProductImageVideo
            token={token}
            productDetail={productDetail}
            setProductDetail={setProductDetail}
            defaultImage={defaultImage}
            selectedImageIndex={selectedImageIndex}
            setSelectedImageIndex={setSelectedImageIndex}
            setDefaultImage={setDefaultImage}
          />

          <div className="grid-span-6 w-full ps-4">
            <div className="border-grey-150 mb-4 border-b-2 pb-4">
              <h3 className="mb-4 text-[22px] font-black capitalize text-black">
                {productDetail?.productNameTrans
                  ? productDetail?.productNameTrans
                  : productDetail?.name}
              </h3>

              {addonlist &&
                addonlist?.["rating-review"] &&
                addonlist?.["rating-review"]?.status == 1 &&
                !isProductDetailLoading &&
                ConnectPlugin.AverageRating && (
                  <ConnectPlugin.AverageRating
                    productDetail={productDetail}
                    reviewAndRatingCount={reviewAndRatingCount}
                    setReviewAndRatingCount={setReviewAndRatingCount}
                    tabChangeScroll={tabChangeScroll}
                  />
                )}
            </div>

            {token &&
            addonlist &&
            addonlist?.["product-price-group"] &&
            addonlist?.["product-price-group"]?.status == 1 &&
            !isProductDetailLoading ? (
              ConnectPlugin.ProductPriceGroup && (
                <ConnectPlugin.ProductPriceGroup
                  productDetail={productDetail}
                />
              )
            ) : (
              <ProductPrice productDetail={productDetail} />
            )}

            {/* Variants */}
            {addonlist &&
              addonlist?.["product-variants"] &&
              addonlist?.["product-variants"]?.status == 1 &&
              ConnectPlugin.ProductVariant && (
                <ConnectPlugin.ProductVariant
                  rawProduct={rawProduct}
                  slug={slug}
                  setIsProductDetailLoading={setIsProductDetailLoading}
                  productDetail={productDetail}
                  setProductDetail={setProductDetail}
                />
              )}

            {/* <GroupedProducts /> */}

            <ProductVendor productDetail={productDetail} />

            {addonlist &&
              addonlist?.["common-catalog"] &&
              addonlist?.["common-catalog"]?.status == 1 &&
              ConnectPlugin.CommonProductVendorCount && (
                <ConnectPlugin.CommonProductVendorCount
                  token={token}
                  productDetail={productDetail}
                />
              )}

            <ProductOrderButtons
              token={token}
              addonlist={addonlist}
              productDetail={productDetail}
              defaultImage={defaultImage}
            />

            <ProductHighlights productDetail={productDetail} />
          </div>
        </div>

        <div>
          <ProductDetailTabs
            addonlist={addonlist}
            activeLink={activeLink}
            setActiveLink={setActiveLink}
            tabChangeScroll={tabChangeScroll}
            productDetail={productDetail}
            reviewAndRatingCount={reviewAndRatingCount}
            sectionMap={sectionMap}
          />

          <div className="px-0">
            <div id="section1">
              <div
                className="mb-6 h-px w-full 2xl:mb-14"
                ref={description}
              ></div>
              <Description productDetail={productDetail} lang={t} />
            </div>

            {productDetail?.isSimplified != 2 &&
              addonlist &&
              addonlist?.["product-attribute"] &&
              addonlist?.["product-attribute"]?.status == 1 && (
                <>
                  <div
                    id="section2"
                    className="bg-grey400 my-6 h-px w-full 2xl:my-14"
                    ref={specification}
                  ></div>
                  {ConnectPlugin.Specification && (
                    <ConnectPlugin.Specification categoryIds={categoryIds} />
                  )}
                </>
              )}

            {addonlist &&
              addonlist?.["rating-review"] &&
              addonlist?.["rating-review"]?.status == 1 &&
              !isProductDetailLoading && (
                <>
                  <div id="section3" ref={Review} className=""></div>
                  {ConnectPlugin.ReviewRating && (
                    <ConnectPlugin.ReviewRating
                      token={token}
                      productDetail={productDetail}
                    />
                  )}
                </>
              )}

            {addonlist &&
              addonlist?.["question-answer"] &&
              addonlist?.["question-answer"]?.status == 1 &&
              !isProductDetailLoading && (
                <>
                  <div
                    className="bg-grey400 my-6 h-px w-full 2xl:my-14"
                    ref={QuesAndAnswer}
                    id="section4"
                  ></div>
                  {ConnectPlugin.QuestionAndAnswer && (
                    <ConnectPlugin.QuestionAndAnswer
                      token={token}
                      productDetail={productDetail}
                    />
                  )}
                </>
              )}

            {addonlist &&
              addonlist?.["product-related"] &&
              addonlist?.["product-related"]?.status == 1 &&
              ConnectPlugin.RelatedProduct && (
                <ConnectPlugin.RelatedProduct token={token} />
              )}
          </div>
        </div>
      </div>

      <Contactus token={token} />
    </>
  );
}
