// React and Next
import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";


const ProductHighlights = ({ productDetail }) => {
  const { t } = useTranslation();
  const [hide, setHide] = useState(true);
  const [showMore, setShowMore] = useState(false);

  useEffect(() => {
    if (productDetail?.productHighlights) {
      // Check if any highlight exceeds 424 characters
      const exceedsLimit = productDetail.productHighlights.some((highlight) => {
        const cleanHtml = getDescription(highlight?.data);
        return cleanHtml.length > 250;
      });
      setShowMore(exceedsLimit);
    }
  }, [productDetail]);

  const getDescription = (description) => {
    const removeHtml = description;
    const cleanHtml = removeHtml?.replace(
      /<p><br>&nbsp;<\/p>|<p>&nbsp;<\/p>| class="[^"]*"| style="[^"]*"/g,
      ""
    );
    return `${cleanHtml} `;
  };

  return (
    productDetail?.productHighlights?.length > 0 && (
      <>
        <h3 className="mb-[11px] font-black text-black text-base">
          {t("myprofile.Itemdetails")}
        </h3>

        <div
          className={`${
            hide
              ? "flex flex-col gap-2.5 h-32 overflow-hidden relative"
              : "flex flex-col gap-2.5 relative h-auto overflow-auto "
          }`}
        >
          {productDetail?.productHighlights?.map(
            (highlights, highlightsindex) => (
              //  Backup style
              //  className="flex items-start gap-2 text-lg font-normal text-black-300 2xl:text-base list-default bullet"

              <p
                key={`highlights${highlightsindex}`}
                className="text-black-300 list-default bullet editor-style"
                dangerouslySetInnerHTML={{
                  __html: getDescription(highlights?.data)
                    ?.replaceAll("&amp;", "&")
                    .replaceAll("&lt;", "<")
                    .replaceAll("&gt;", ">")
                    .replaceAll("&quot;", '"')
                    .replaceAll("&#39;", "'")
                    .replaceAll("&sbquo;", "‚")
                    .replaceAll("&#61;", "=")
                    .replaceAll("&#45;", "-")
                    .replaceAll("&hellip;", "…")
                    .replaceAll("&commat;", "@")
                    .replaceAll("&copy;", "©")
                    .replaceAll("&#35;", "#")
                    .replaceAll("&ldquo;", "“")
                    .replaceAll("&rsquo;", "’")
                    .replaceAll("&lsquo;", "‘")
                    .replaceAll("&trade;", "™")
                    .replaceAll("&reg;", "®")
                    .replaceAll("&ndash;", "–")
                    .replaceAll("&eacute;", "é")
                    .replaceAll("&euro;", "€")
                    .replaceAll("&pound;", "£")
                    .replace(
                      "&lt;/p&gt;&amp;&#35;10;&amp;&#35;10;&lt;p&gt;",
                      ""
                    ),
                }}
              />
            )
          )}
          {showMore && (
            <div className={`${hide ? "hide-out" : "hidden"}`}></div>
          )}
        </div>
        {showMore && (
          <div className="flex justify-center">
            <button
              className={`${
                hide
                  ? "px-3 rounded flex items-center justify-center text-xs 2xl:text-sm font-medium relative text-boldblack h-7 style-2 "
                  : "hidden"
              }`}
              onClick={() => setHide(false)}
            >
              {t("myprofile.keyfeatures")}
            </button>
            <button
              className={`${
                hide
                  ? " hidden"
                  : "px-3 rounded flex items-center justify-center  text-xs 2xl:text-sm font-medium relative text-boldblack h-7 style-2 mt-4"
              }`}
              onClick={() => setHide(true)}
            >
              {t("myprofile.Showless")}
            </button>
          </div>
        )}
      </>
    )
  );
};

export default ProductHighlights;
