"use client";

//Utils
import { PriceCalculate, PriceTaxCalculate } from "@/utilities/PriceCalculate";

//Components
import CustomCurrencyFormatter from "@/Components/CustomCurrencyFormatter";
import { useTranslation } from "react-i18next";

export default function ProductPrice({ productDetail }) {
  const { t } = useTranslation();
  
  return (
    <div className="pb-2 mb-4 border-b-2 border-grey-150">
      <h3 className="text-black text-[28px] font-black mb-0 2xl:mb-2.5">
        <CustomCurrencyFormatter
          price={PriceCalculate(
            productDetail?.price,
            productDetail?.pricerefer,
            productDetail?.flag,
            productDetail?.taxType,
            productDetail?.taxValue
          )}
        />
      </h3>
      {productDetail?.flag == 1 && (
        <div className="flex items-center space-x-2">
          <p className="text-sm font-normal text-black-300">
            {t("productdetails.Mrp")}{" "}
            <del>
              <CustomCurrencyFormatter
                price={PriceTaxCalculate(
                  productDetail?.price,
                  productDetail?.taxType,
                  productDetail?.taxValue
                )}
              />
            </del>
          </p>
          <span className="text-sm font-normal text-activegreen">
            {Math.round(
              ((PriceTaxCalculate(
                productDetail?.price,
                productDetail?.taxType,
                productDetail?.taxValue
              ) -
                PriceTaxCalculate(
                  productDetail?.pricerefer,
                  productDetail?.taxType,
                  productDetail?.taxValue
                )) /
                PriceTaxCalculate(
                  productDetail?.price,
                  productDetail?.taxType,
                  productDetail?.taxValue
                )) *
                100
            )}
            {t("productdetails.OFF")}
          </span>
        </div>
      )}
    </div>
  );
}
