import { useRef, useState, useEffect } from "react";
import MagnifierPreview from "../MagnifierPreview";
import { imageUrl } from "@/utilities/url";

const ImageHover = ({ defaultImage, setResize }) => {
  const imageSrc =
    imageUrl +
    "?path=" +
    defaultImage?.containerName +
    "&name=" +
    defaultImage?.image +
    "&width=1000&height=1000";
  const imageRef = useRef(null);
  const [hoverPosition, setHoverPosition] = useState(null);
  const [hasError, setHasError] = useState(false);
  const animationFrame = useRef(null);
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth < 768);
    };

    handleResize();
    window.addEventListener("resize", handleResize);
    return () => window.removeEventListener("resize", handleResize);
  }, []);

  const handleMouseMove = (e) => {
    if (isMobile || hasError || !imageRef.current) return;

    const rect = imageRef.current.getBoundingClientRect();
    const x = e.clientX - rect.left;
    const y = e.clientY - rect.top;

    // Clamp x and y to stay within image and keep the lens inside
    const clampedX = Math.max(
      lensSize / 2,
      Math.min(rect.width - lensSize / 2, x),
    );
    const clampedY = Math.max(
      lensSize / 2,
      Math.min(rect.height - lensSize / 2, y),
    );

    setHoverPosition({ x: clampedX, y: clampedY });

    if (animationFrame.current) cancelAnimationFrame(animationFrame.current);

    animationFrame.current = requestAnimationFrame(() => {
      window.dispatchEvent(
        new CustomEvent("update-magnifier", {
          detail: {
            show: true,
            position: { x: clampedX, y: clampedY },
            imageSrc,
            dimensions: {
              width: rect.width,
              height: rect.height,
            },
            boundingBox: {
              top: rect.top + window.scrollY,
              height: rect.height,
            },
          },
        }),
      );
    });
  };

  const handleMouseLeave = () => {
    if (animationFrame.current) {
      cancelAnimationFrame(animationFrame.current);
    }
    setHoverPosition(null);

    window.dispatchEvent(
      new CustomEvent("update-magnifier", {
        detail: { show: false },
      }),
    );
  };

  const handleImageError = ({ currentTarget }) => {
    setHasError(true);
    currentTarget.onerror = null;
    currentTarget.src = "/img/no-image.png";
    currentTarget.alt = "no-image";
    window.dispatchEvent(
      new CustomEvent("update-magnifier", {
        detail: { show: false },
      }),
    );
  };

  const lensSize = isMobile ? 0 : 250;

  return (
    <>
      <div className="relative flex h-full w-full items-center">
        <img
          src={imageSrc}
          alt={defaultImage?.image}
          ref={imageRef}
          onClick={() => setResize(true)}
          onMouseMove={handleMouseMove}
          onMouseLeave={handleMouseLeave}
          onError={handleImageError}
          className="m-auto h-full w-full cursor-crosshair object-contain"
          loading="lazy"
        />

        {!isMobile && !hasError && hoverPosition && (
          <div
            // border-2 border-blue-500
            className="pointer-events-none absolute rounded-2xl bg-blue-200/30 shadow"
            style={{
              width: `${lensSize}px`,
              height: `${lensSize}px`,
              left: `${hoverPosition.x - lensSize / 2}px`,
              top: `${hoverPosition.y - lensSize / 2}px`,
            }}
          />
        )}
      </div>

      <MagnifierPreview />
    </>
  );
};

export default ImageHover;
