"use client";

// React and Next
import { useEffect, useState } from "react";

// 3rd Party
import { useRouter } from "nextjs-toploader/app";
//Redux
import { useDispatch, useSelector } from "react-redux";

// API
import { imageUrl } from "@/utilities/url";
import envService from "@/utilities/env.service";

// Components
import {
  AddWishListApi,
  WishListDeleteApi,
} from "@/api/clientsideaction/productsearch";
import ResizeImageVideo from "./ResizeImageVideo";
import ImageHover from "./ImageHover";

export default function ProductImageVideo({
  token,
  productDetail,
  setProductDetail,
  defaultImage,
  selectedImageIndex,
  setSelectedImageIndex,
  setDefaultImage,
}) {
  const router = useRouter();
  const dispatch = useDispatch();
  const [reszize, setResize] = useState(false);
  const [videoSrc, setVideoSrc] = useState(null);
  const [videoPaused, setVideoPaused] = useState(true);
  const [videoPausedSec, setVideoPausedSec] = useState(true);
  const [videoRef, setVideoRef] = useState(null);
  const [videoRefSec, setVideoRefSec] = useState(null);
  const [isAddToWishlistLoading, setIsAddToWishlistLoading] = useState(false);
  const wishCount = useSelector((state) => state.userReducer.wishCount);

  let newProductArray;
  if (Array.isArray(productDetail?.productOriginalImage)) {
    if (productDetail?.productVideo && productDetail?.productVideo?.name) {
      newProductArray = [
        ...productDetail?.productOriginalImage,
        productDetail?.productVideo,
      ];
    } else {
      newProductArray = productDetail?.productOriginalImage;
    }
  } else {
    newProductArray = productDetail?.productOriginalImage;
  }

  useEffect(() => {
    if (reszize == true) {
      document.body.classList.add("overflow-hidden");
    } else {
      document.body.classList.remove("overflow-hidden");
    }
  }, [reszize]);

  useEffect(() => {
    const fetchVideo = async () => {
      try {
        const config = {
          method: "GET",
        };

        const response = await fetch(
          `${envService.getApiBaseUrl()}media/video-preview-s3?name=${productDetail?.productVideo?.name}&path=${productDetail?.productVideo?.path}`,
          config,
        );

        if (response.ok) {
          const blob = await response.blob();
          const reader = new FileReader();
          reader.readAsDataURL(blob);
          reader.onloadend = function () {
            setVideoSrc(reader.result);
          };
        } else {
          console.error("Fetch error:", response.statusText);
        }
      } catch (error) {
        console.error("Error fetching video:", error);
      }
    };
    if (productDetail?.productVideo?.name) {
      fetchVideo();
    }
  }, [productDetail]);

  useEffect(() => {
    if (videoRef) {
      videoRef.addEventListener("play", () => setVideoPaused(false));
      videoRef.addEventListener("pause", () => setVideoPaused(true));
    }
    if (videoRefSec) {
      videoRefSec.addEventListener("play", () => setVideoPausedSec(false));
      videoRefSec.addEventListener("pause", () => setVideoPausedSec(true));
    }
  }, [videoRef, videoRefSec]);

  const handleImageChange = (img, index) => {
    setDefaultImage(JSON.parse(JSON.stringify(img)));
    setSelectedImageIndex(index + 1);
  };

  const handlePreviousImage = () => {
    newProductArray?.forEach((imagelist, index) => {
      if (imagelist?.image == defaultImage?.image) {
        if (index == 0) {
          setSelectedImageIndex(newProductArray?.length);
          const lastImage = newProductArray?.at(-1);
          setDefaultImage(JSON?.parse(JSON?.stringify(lastImage)));
        } else {
          setSelectedImageIndex(index);
          const previousImage = newProductArray?.find((_, i) => i == index - 1);
          setDefaultImage(JSON?.parse(JSON?.stringify(previousImage)));
        }
      }
    });
  };

  const handleNextImage = () => {
    newProductArray?.forEach((imagelist, index) => {
      if (imagelist?.image == defaultImage?.image) {
        if (index + 1 == newProductArray?.length) {
          setSelectedImageIndex(1);
          const firstImage = newProductArray?.at(0);
          setDefaultImage(JSON?.parse(JSON?.stringify(firstImage)));
        } else {
          setSelectedImageIndex(index + 2);
          const nextImage = newProductArray?.find((_, i) => i == index + 1);
          setDefaultImage(JSON?.parse(JSON?.stringify(nextImage)));
        }
      }
    });
  };

  const handleWishList = async () => {
    if (!token) {
      router.push("/buyer-login");
      return;
    }

    setIsAddToWishlistLoading(true);
    if (productDetail?.wishListStatus == 0) {
      const result = await AddWishListApi(
        productDetail?.productId,
        dispatch,
        wishCount,
      );
      if (result?.data?.status == 1) {
        setProductDetail((pre) => ({
          ...pre,
          wishListStatus: 1,
        }));
      }
    } else {
      const result = await WishListDeleteApi(
        productDetail?.productId,
        dispatch,
        wishCount,
      );
      if (result?.data?.status == 1) {
        setProductDetail((pre) => ({
          ...pre,
          wishListStatus: 0,
        }));
      }
    }
    setIsAddToWishlistLoading(false);
  };

  return (
    <>
      <div className="grid-span-10 static flex w-full flex-col-reverse space-x-[unset] space-y-2 sm:sticky sm:top-[106px] lg:flex-row lg:space-x-2 lg:space-y-0 lg:rtl:space-x-[unset]">
        {/* Images and Video list */}
        <div className="scroll-hide group relative ml-0 mt-2 hidden max-h-[592.5px] min-w-full flex-row overflow-auto sm:flex sm:min-w-[96px] sm:space-x-2 lg:flex-col lg:space-x-0 lg:space-y-2 lg:rtl:ml-2">
          {newProductArray &&
            newProductArray?.map(
              (imagelist, index) =>
                imagelist?.mediaType == 1 && (
                  <a
                    role="button"
                    key={`image${index}`}
                    onClick={(e) => handleImageChange(imagelist, index)}
                    aria-label={`${imagelist?.image || index}`}
                    className={
                      imagelist?.image == defaultImage?.image
                        ? `group relative aspect-square max-h-[96px] min-w-[96px] cursor-pointer overflow-hidden rounded-lg border-2 border-black hover:border-2 hover:border-black lg:w-full rtl:ml-2`
                        : "group relative aspect-square max-h-[96px] min-w-[96px] cursor-pointer overflow-hidden rounded-lg border-2 hover:border-2 hover:border-black lg:w-full"
                    }
                  >
                    <img
                      src={
                        imageUrl +
                        "?path=" +
                        imagelist?.containerName +
                        "&name=" +
                        imagelist?.image +
                        "&width=300&height=300"
                      }
                      alt={imagelist?.image}
                      onError={({ currentTarget }) => {
                        currentTarget.onerror = null;
                        currentTarget.src = "/img/no-image.png";
                        currentTarget.alt = "no-image";
                      }}
                      className="m-auto h-full w-full object-contain"
                    />
                  </a>
                ),
            )}

          {/* Video */}
          {newProductArray &&
            newProductArray?.map(
              (imagelist, index) =>
                imagelist?.mediaType == 2 &&
                imagelist?.name && (
                  <a
                    role="button"
                    key={`video${index}`}
                    onClick={(e) => handleImageChange(imagelist, index)}
                    aria-label={`${imagelist?.image || index}`}
                    className={
                      imagelist?.image == defaultImage?.image
                        ? `group relative aspect-square max-h-[96px] min-w-[96px] cursor-pointer overflow-hidden rounded-lg border-2 border-black hover:border-2 hover:border-black lg:w-full`
                        : "group relative aspect-square max-h-[96px] min-w-[96px] cursor-pointer overflow-hidden rounded-lg border-2 hover:border-2 hover:border-black lg:w-full"
                    }
                  >
                    <video
                      src={videoSrc}
                      className="m-auto h-full w-full object-contain"
                    ></video>
                  </a>
                ),
            )}
        </div>

        {/* Selected Image or Video */}
        <div className="min-w-calc89 relative aspect-square max-h-[592.5px] rounded-2xl bg-gray-100">
          {defaultImage?.mediaType == 1 ? (
            // <img
            //   src={
            //     imageUrl +
            //     "?path=" +
            //     defaultImage?.containerName +
            //     "&name=" +
            //     defaultImage?.image +
            //     "&width=600&height=500"
            //   }
            //   alt={defaultImage?.image}
            //   className="object-contain w-full h-full m-auto cursor-pointer"
            //   onClick={() => setResize(true)}
            //   onError={({ currentTarget }) => {
            //     currentTarget.onerror = null;
            //     currentTarget.src = "/img/no-image.png";
            //     currentTarget.alt = "no-image";
            //   }}
            // />
            <ImageHover defaultImage={defaultImage} setResize={setResize} />
          ) : (
            <video
              src={videoSrc}
              className="m-auto h-full w-full cursor-pointer object-contain"
              autoPlay
              ref={(video) => setVideoRef(video)}
              onClick={() => {
                if (videoPaused) {
                  videoRef.play();
                } else {
                  videoRef.pause();
                }
              }}
              // onClick={() => {
              //     if (videoPausedSec) {
              //         videoRefSec.play();
              //     } else {
              //         videoRefSec.pause();
              //     }
              // }}
              // ref={(video) => setVideoRefSec(video)}
            />
          )}

          {/* Previous and Next buttons */}
          <button
            onClick={(e) => handlePreviousImage()}
            className="absolute left-2 top-[45%] flex h-12 w-12 items-center justify-center rounded-full bg-white shadow-lg hover:bg-gray-100"
          >
            <img src="/img/leftgray- arrow.svg" alt="leftgray-arrow" />
          </button>
          <button
            onClick={(e) => handleNextImage()}
            className="absolute right-2 top-[45%] flex h-12 w-12 items-center justify-center rounded-full bg-white shadow-lg hover:bg-gray-100"
          >
            <img src="/img/rightgray-arrow.svg" alt="rightgray-arrow" />
          </button>

          {/* Resize button */}
          <button
            className="absolute right-4 top-4 hidden h-12 w-12 items-center justify-center rounded-full bg-white shadow-lg hover:bg-gray-100 sm:flex"
            onClick={() => setResize(true)}
          >
            <img src="/img/expand.svg" alt="expand" />
          </button>

          {/* Wishlist button */}
          <button
            disabled={isAddToWishlistLoading}
            onClick={handleWishList}
            className={
              productDetail?.wishListStatus == 0
                ? "item tooltip2 absolute right-4 top-20 hidden h-12 w-12 cursor-pointer items-center justify-center rounded-full bg-white shadow-lg hover:bg-gray-100 sm:flex"
                : `tooltip2 absolute right-4 top-20 hidden h-12 w-12 cursor-pointer items-center justify-center rounded-full bg-white shadow-lg hover:bg-gray-100 sm:flex`
            }
          >
            {isAddToWishlistLoading ? (
              <span className="h-5 w-5 animate-spin rounded-full border-2 border-gray-300 border-t-gray-600 dark:border-gray-600 dark:border-t-white" />
            ) : productDetail?.wishListStatus == 1 ? (
              <img
                src="/img/heart-select-red-wisit.svg"
                alt="heart-select-red-wisit"
              />
            ) : (
              <img
                src="/img/heart-outline-wisit.svg"
                alt="heart-outline-wisit"
              />
            )}

            {/* <span className="tooltip-text">{language?.Breadcrumb.wishList}</span> */}
          </button>
        </div>
      </div>

      {/* Resize Modal */}
      <ResizeImageVideo
        reszize={reszize}
        setResize={setResize}
        selectedImageIndex={selectedImageIndex}
        newProductArray={newProductArray}
        defaultImage={defaultImage}
        videoSrc={videoSrc}
        handlePreviousImage={handlePreviousImage}
        handleNextImage={handleNextImage}
        handleImageChange={handleImageChange}
      />
    </>
  );
}
